import { Component, OnInit,OnChanges, Input } from '@angular/core'
import { DataService } from '@services/data.service'

@Component({
  selector: 'app-project-teams',
  templateUrl: './project-teams.component.html',
})
export class ProjectTeamsComponent implements OnInit, OnChanges {

  @Input("project") project: any

  teams: Array<any>
  team: any
  users: Array<any>
  availableUsers: Array<any> = []
  removedUsers:Array<any> = []
  addedUsers:Array<any> = []

  displayDeleteDialog: Boolean
  displayTeamDialog: Boolean

  filter: String

  constructor(
    private dataService:DataService
  ) { }

  ngOnInit() {

  }

  ngOnChanges(){
    this.onFindTeams()
    this.onFindAvailableSellers()
  }

  onFindTeams(){
    console.log(this.project)
    if(this.project._id){
      this.dataService.findById("team", this.project._id).subscribe((data)=>{
        this.teams = data
      })
    }
  }

  onFindAvailableSellers(){
    if(this.project._id){
      this.dataService.find("users/available/sellers").subscribe((data)=>{
        this.availableUsers = data
      })
    }
  }

  onShowDeleteDialog(team){
    this.team = team
    this.displayDeleteDialog = true
  }

  onAddMember(member, index){
    if(!this.team.members){
      this.team.members = []
    }
    member.team = this.team._id
    member.team_name = this.team.name
    member.project = this.project._id
    member.project_name = this.project.name
    this.addedUsers.push(member)
    this.team.members.push({id_user : member._id, name: member.name})
    this.availableUsers.splice(index, 1)
  }

  onRemoveMember(index){
    var users
    var member
    if(this.team.members){
      users = this.team.members.splice(index, 1)
      this.dataService.findById("users", users[0].id_user).subscribe((data)=>{
        member = data
        delete  member.team
        delete  member.team_name
        delete  member.project
        delete  member.project_name
        this.removedUsers.push(member)
      })
    }
  }

  onEdit(team){
    this.onFindAvailableSellers()
    this.team = team
    this.displayTeamDialog = true
  }

  onNew(){
    this.onFindAvailableSellers()
    this.team = {members:[]}
    this.team.coordinates = {}
    this.displayTeamDialog = true
  }

  onCancel(){
    this.team = { members:[] }
    this.removedUsers= []
    this.addedUsers = []
    this.displayTeamDialog = false
    this.onFindAvailableSellers()
    this.onFindTeams()
  }

  onSave(){
    this.team.type="team"
    this.team.project=this.project._id
    let promises  = []
    promises.push(new Promise((resolve, reject) => {
      this.dataService.insertOne("team", { team: this.team, id: this.project._id }).subscribe((data)=>{
        resolve(data)
      }, (err) =>{
        reject(err)
      })
    }))

    promises.push(new Promise((resolve, reject) => {

      console.log("save users: ", this.addedUsers)
      this.dataService.insertOne("users", { users: this.addedUsers }).subscribe((data)=>{
        resolve(data)
      }, (err) =>{
        reject(err)
      })
    }))

    promises.push(new Promise((resolve, reject) => {
      this.dataService.insertOne("users", { users: this.removedUsers }).subscribe((data)=>{
        resolve(data)
      }, (err) =>{
        reject(err)
      })
    }))

    Promise.all(promises).then(() => {
      this.onFindTeams()
      this.onFindAvailableSellers()
      this.displayTeamDialog = false
    })

  }

  onDelete(){
    this.dataService.deleteOne("team", this.team._id + "/" + this.team._rev).subscribe((data)=>{
      console.log(data)
      this.displayDeleteDialog = false
      this.onFindTeams()
    })
  }

}
