import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core'
import { DataService } from '@services/data.service';

@Component({
  selector: 'app-project-clients',
  templateUrl: './project-clients.component.html',
})
export class ProjectClientsComponent implements OnInit {

  @Input("project") project: any
  @Output() onSaveEvent = new EventEmitter();

  displayDeleteDialog: Boolean
  displayAddDialog: Boolean
  index: any
  client: any
  clients: Array<any>
  filter: any;

  constructor(
    private dataService:DataService
  ) { }

  ngOnInit() {
  }

  onFindClients(){
    this.dataService.find("users/clients").subscribe((data)=>{
      this.clients = data;
    })
  }

  onShowDeleteDialog(index, client){
    this.index = index
    this.client = client
    this.displayDeleteDialog = true;
  }

  onSave(){
    this.onSaveEvent.emit()
  }

  onDelete(){
    if(this.project.members){
      this.project.members.splice(this.index,1)
      this.onSave()
    }
    this.displayDeleteDialog = false;
  }

  onAdd(user){
    if(!this.project.members){
      this.project.members= []
    }
    this.project.members.push({name: user.name, id_user: user._id})
    this.onSave()
    this.displayAddDialog = false;
  }

  onNew(){
    this.onFindClients()
    this.displayAddDialog = true;
  }

}
