import { Component, OnInit } from '@angular/core';
import { DataService } from '@services/data.service';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { MessageService } from 'primeng/api';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
})
export class UserComponent implements OnInit {

  userId:any;
  user:any;
  roles: Array<any>;
  selectedRole:any;
  password:any;

  constructor(
    private dataService:DataService,
    private route: ActivatedRoute,
    private router: Router,
    private messageService:MessageService


  ) { }

  ngOnInit() {

    this.roles = [
        {name: 'Administrador', code: 'admin'},
        {name: 'Vendedor', code: 'vendedor'},
        {name: 'Supervisor', code: 'supervisor'},
        {name: 'Cliente', code: 'cliente'},
    ];

    this.user = {
      roles:[],
      type:"user"
    };
    this.route.params.subscribe((params : Params) => {
      this.userId =  params["id"];
      if(this.userId){
        this.onFindUser();
      }
    });
  }

  onFindUser(){
    console.log("userId",this.userId);
    this.dataService.findById('users',this.userId).subscribe((data)=>{
      console.log("data",data);
      this.user = data;
    });
  }

  onChangeRole(){
    this.user.roles.push(this.selectedRole.code);
  }

  onAddRole(role){
    if(this.user && this.user.roles && role){
      this.user.roles.push(role);
    }
  }

  onRemoveRole(index){
    if(this.user && this.user.roles){
      this.user.roles.splice(index, 1);
    }
  }

  onSave(){
    this.dataService.insertOne("users", {"user":this.user}).subscribe(()=>{
      console.log("User Saved");
      this.onSavePassword();
      this.messageService.add({severity:'success', summary:'', detail:'Usuario guardado'})
      this.router.navigate(['/admin/users/list']);
    });
  }

  onSavePassword(){
    if(this.user.email && this.password){
      this.dataService.insertOne("password", {email:this.user.username, password: this.password}).subscribe(()=>{
        console.log("Password Saved");
      })
    }
  }

}
