import { Component, OnInit } from '@angular/core';
import { DataService }        from '@services/data.service';
import { SecurityService } from '@services/security.service';

import * as _ from 'lodash';

@Component({
  selector: 'app-evidences',
  templateUrl: './evidences.component.html',
})
export class EvidencesComponent implements OnInit {

  projects:Array<any>;
  project:any;
  chains:Array<any>;
  chain:any;
  stores:Array<any>;
  store:any;
  evidences:any;

  startDate:any = new Date();
  endDate:any = new Date();

  constructor(
    private dataService: DataService,
    private securityService: SecurityService

  ) { }

  ngOnInit() {
    var user = this.securityService.getCurrentUser()
    if(this.securityService.hasPermisions(['admin'])){
      this.onFindProyects();
    }else{
      this.onFindProyectsByUser(user._id)
    }
  }

  onFindProyects(){
    this.dataService.find("project").subscribe((data) => {
      var projects = [];
      data.rows.forEach(function(element) {
        projects.push(element.doc);
      });
      this.projects = projects;
      this.project = this.projects[0] || {} ;
      this.onFindChains();
      this.onFindStores();
    });
  }

  onFindProyectsByUser(userId){
    this.dataService.find("users/"+userId+"/projects").subscribe((data) => {
      this.projects = data.docs;
      this.project = this.projects[0];
      this.onFindChains();
      this.onFindStores();
    });
  }

  onChangeProject(){
    this.onFindChains();
    this.onFindStores();
  }

  onFindChains(){
    console.log("onFindChains");
    this.dataService.findBy("chain", this.project._id).subscribe((data) => {
      this.chains = data;
      this.chain = this.chains[0] || "";
    });
  }

  onFindStores(){
    console.log("onFindStores");
    this.dataService.findBy("evidences/project", this.project._id + "/stores").subscribe((data) => {
      this.stores = data;
      this.store = this.stores[0] || "";
    });
  }

  onFind(){
    this.onFindEvidences();
  }

  onFindEvidences(){
    console.log("onFindEvidences");
    this.dataService.findBy("evidences/project", this.project._id + '/chain/' + this.chain.name + "/store/" + this.store.key[2] +'/'+this.startDate+'/'+this.endDate).subscribe((data) => {
      console.log(data);
      this.evidences = [];

      data.forEach((evidence)=>{
        var ticket = {
          chain_name: evidence.doc.chain_name,
          store: evidence.doc.store,
          city: evidence.doc.city,
          created_at: evidence.doc.created_at,
          comment: evidence.doc.comment,
          photos: []
        };
        if(evidence.doc.evidencePhotos){
          evidence.doc.evidencePhotos.forEach((photo)=>{
            ticket.photos.push({source: photo, alt:'', title:''})
          })
        }else{
          ticket.photos.push({source: "assets/images/helpers/Sin_imagen_disponible.jpg", alt:'', title:''})
        }
        this.evidences.push(ticket);
      })
    });
  }

}
