import { Component, OnInit, ViewChild } from '@angular/core'
import { DataService } from '@services/data.service'
import { ExcelService } from '@services/excel.service'
import { SecurityService } from '@services/security.service'

import * as _ from 'lodash'
import * as moment from 'moment'


@Component({
  selector: 'app-teams',
  templateUrl: './teams.component.html',
})
export class TeamsComponent implements OnInit {

  @ViewChild('salesbyTeamChart', { static: true }) salesbyTeamChart
  @ViewChild('salesbyTeamStoreChart', { static: true }) salesbyTeamStoreChart
  @ViewChild('salesbyTeamLineChart', { static: true }) salesbyTeamLineChart


  colors = ['#4bc0c0', '#ec7063', '#b078c7', '#73c6b6', '#73c6b6', '#f6b7b0', '#f9e89b', '#808b96', '#885656', '#D4E4BC', '#96ACB7', '#36558F', '#40376E', '#48233C', '#E7E6F7', '#E3D0D8', '#AEA3B0', '#827081', '#F7F7F2', '#E4E6C3', '#899878', '#222725', '#121113']
  spiner = {
    salesbyTeamLine:false,
    topSalesProduct:false,
    topSalesStore:false,
    topSalesProductsStore:false,

  }

  topSalesProductsExpand
  topSalesProductsStoreExpand
  topSalesStoreExpand
  salesbyTeamExpand
  salesbyTeamLineExpand

  projects:any
  project:any
  startDate:any = new Date()
  endDate:any = new Date()

  cities: any
  city:any

  teams: any
  team:any

  topSalesProduct:any = 0
  totalSale:any= 0
  totalProducts:any= 0
  totalPieces:any= 0
  totalImpacts:any= 0
  topSalesProducts:any
  topSalesProductsStore:any
  topSalesStore:any


  salesbyTeamLine:any
  salesbyTeamData:any
  topSalesStoreData:any
  topSalesStoreOptions:any;
  salesbyTeamLineData:any

  workbook:any
  timeformat = "YYYY-MM-DD"

  optionsDonut:any;
  options:any;

  constructor(
    private dataService: DataService,
    private excelService: ExcelService,
    private securityService: SecurityService
  ) {

    this.options ={
      tooltips: {
         callbacks: {
            label: function(t, d) {
               var xLabel = d.datasets[t.datasetIndex].label;
               var value  = t.yLabel.toFixed(2);
               var yLabel = value >= 1000 ? value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : value;
               return xLabel + ': $ ' + yLabel;
            }
         }
      },
      scales: {
          yAxes: [
              {
                  ticks: {
                      callback: function(label) {
                          return '$' + label;
                      }
                  }
              }
          ]
      }
    };

    this.optionsDonut ={
      tooltips: {
         callbacks: {
            label: function(t, d) {
               var xLabel = d.labels[t.index];
               var value  = d.datasets[t.datasetIndex].data[t.index].toFixed(2);
               var yLabel = value >= 1000 ? value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : value;
               return xLabel + ': $ ' + yLabel;
            }
         }
      }
    };

    this.salesbyTeamData = {
      labels: [],
      datasets: [{
        label: 'Ventas',
        backgroundColor: '#42A5F5',
        borderColor: '#1E88E5',
        data: []
      }]
    }

    this.topSalesStoreData = {
      labels: [],
      datasets: [{
        label: 'Cantidad',
        backgroundColor: '#ffa300',
        borderColor: '#ff6600',
        borderWidth: 1,
        pointRadius: 10,
        showLine: true,
        fill: false,
        pointStyle: 'rectRot',
        type: 'line',
        yAxisID: 'A',
        data: []
      },
      {
        label: 'Ventas',
        borderWidth: 1,
        type: 'bar',
        backgroundColor: '#f1336788',
        borderColor: '#bf1b48',
        yAxisID: 'B',
        data: []
      }]
    };

    this.topSalesStoreOptions ={
      tooltips: {
         callbacks: {
            label: function(t, d) {
               var xLabel = d.datasets[t.datasetIndex].label;
               var value  = t.yLabel.toFixed(2);
               var yLabel = value >= 1000 ? value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : value;
               if( xLabel == "Ventas" ){
                 return xLabel + ': $ ' + yLabel;
               }else{
                 return xLabel + ': ' + yLabel;
               }
            }
         }
      },
      scales: {
        yAxes: [{
          id: 'A',
          type: 'linear',
          position: 'left',
        }, {
          id: 'B',
          type: 'linear',
          position: 'right',
          ticks: {
              callback: function(label) {
                  return '$' + label;
              }
          }
        }]
      }
    };

    this.salesbyTeamLineData = {
      labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
      datasets: []
    }

   }

   ngOnInit() {
     var user = this.securityService.getCurrentUser()
     if(this.securityService.hasPermisions(['admin'])){
       this.onFindProyects()
     }else{
       this.onFindProyectsByUser(user._id)
     }
     this.startDate = this.dataService.filters.startDate
     this.endDate = this.dataService.filters.endDate
   }

   onChangeStartDate(){
     this.dataService.filters.startDate = this.startDate
   }

   onChangeEndDate(){
     this.dataService.filters.endDate = this.endDate
   }

   onChangeProject(){
     this.dataService.filters.project = this.project
     this.onFindCitiesbyProject()
   }

   onCompareById(item1, item2) {
     if(item1 && item2){
       return item1._id === item2._id
     }
     else{
       return false
     }
   }

  onFind(){
    this.onFindTopSalesProduct()
    this.onFindTopSalesProductByStore()
    this.onFindTopSalesStore()
    this.onFindTotalImpacts()
    this.onFindSalesProductbyTeamLine()
  }

  onChangeCity(){
    this.onFindTeamsbyProjectAndCity()
  }

  onFindProyects(){
    this.dataService.find("project").subscribe((data) => {
      this.projects = []
      data.rows.forEach(function(element) {
        this.projects.push(element.doc)
      }, this)
      this.project = this.dataService.filters.project || this.projects[0] || {}
      this.onFindCitiesbyProject()
    })
  }

  onFindProyectsByUser(userId){
    this.dataService.find("users/"+userId+"/projects").subscribe((data) => {
      this.projects = data.docs
      this.project = this.projects[0] || {}
      this.onFindCitiesbyProject()
    })
  }

  onFindCitiesbyProject(){
    this.dataService.findBy("sales/project", this.project._id +"/cities").subscribe((data) => {
      this.cities = data
      this.city = this.cities[0]
      this.onFindTeamsbyProjectAndCity()
    })
  }

  onFindTeamsbyProjectAndCity(){
    this.team = "*"
    this.dataService.findById("team", this.project._id).subscribe((data)=>{
      this.teams = data
    })
  }

  onFindTopSalesProduct(){
    this.spiner['topSalesProduct'] = true
    let team = ""
    if(this.team != "*"){
      team = '/team/'+ this.team._id
    }

    this.dataService.findBy("sales/project", this.project._id + '/city/'+ this.city + team + "/product/amount/"+this.startDate+"/"+this.endDate+"/10").subscribe((data) => {
      this.topSalesProducts = []
      this.spiner['topSalesProduct'] = false
      this.topSalesProducts = data

      this.onUpdateTotals()

      this.salesbyTeamData.labels= []
      this.salesbyTeamData.datasets[0].data = []

      for(var i = 0; i < this.topSalesProducts.length; i++) {
        this.salesbyTeamData.labels.push(this.dataService.formatLabel(this.topSalesProducts[i].name, 10))
        this.salesbyTeamData.datasets[0].data.push(this.topSalesProducts[i].amount)
      }

      this.salesbyTeamChart.refresh()
    })
  }

  onFindTopSalesProductByStore(){
    this.spiner['topSalesProductsStore'] = true
    let team = ""
    if(this.team != "*"){
      team = '/team/'+ this.team._id
    }

    this.dataService.findBy("sales/project", this.project._id + '/city/'+ this.city + team + "/product/store/"+this.startDate+"/"+this.endDate+"/10").subscribe((data) => {
      this.topSalesProductsStore = []
      this.topSalesProductsStore = data
      this.spiner['topSalesProductsStore'] = false
    })
  }

  onFindTopSalesStore(){
    this.spiner['topSalesStore'] = true
    let team = ""
    if(this.team != "*"){
      team = '/team/'+ this.team._id
    }

    this.dataService.findBy("sales/project", this.project._id + '/city/'+ this.city + team + "/store/"+this.startDate+"/"+this.endDate+"/10").subscribe((data) => {
      this.topSalesStore = []
      this.topSalesStore = data

      this.topSalesStoreData.labels = [];
      this.topSalesStoreData.datasets[0].data = [];
      this.topSalesStoreData.datasets[1].data = [];

      for(var i = 0; i < this.topSalesStore.length; i++) {
        this.topSalesStoreData.labels.push(this.dataService.formatLabel(this.topSalesStore[i].store, 10));
        this.topSalesStoreData.datasets[0].data.push(this.topSalesStore[i].pieces);
        this.topSalesStoreData.datasets[1].data.push(this.topSalesStore[i].amount);
      }

      this.salesbyTeamStoreChart.refresh()
      this.spiner['topSalesStore'] = false

    })
  }

  onUpdateTotals(){
    let count = 0
    let amount = 0
    let pieces = 0
    this.topSalesProducts.forEach((element)=>{
      count++
      amount = amount + element.amount
      pieces = pieces + element.pieces
    })
    this.totalSale = amount
    this.totalProducts = count
    this.totalPieces = pieces
  }

  onFindTotalImpacts(){
    let team = ""
    if(this.team != "*"){
      team = '/team/'+ this.team._id
    }
    this.dataService.findBy("impacts/project", this.project._id + '/city/'+ this.city + team + "/total/"+this.startDate+"/"+this.endDate).subscribe((data) => {
      this.totalImpacts = data.totalimpacts
    })
  }

  onFindSalesProductbyTeamLine(){
    this.spiner["salesbyTeamLine"] = true
    let team = ""
    if(this.team != "*"){
      team = '/team/'+ this.team._id
    }
    var frequency = "daily"
    if (this.startDate.toDateString() === this.endDate.toDateString()) {
      frequency = "hourly"
    }

    this.salesbyTeamLineData.labels = []
    this.salesbyTeamLineData.datasets = []

    this.dataService.findBy("sales/project", this.project._id + '/city/'+ this.city + team +"/product/top/amount/"+ frequency +"/"+this.startDate+"/"+this.endDate+"/10").subscribe((data) => {
      this.salesbyTeamLine = data
      this.salesbyTeamLineData.labels = this.salesbyTeamLine.labels
      for (let i = 0; i < this.salesbyTeamLine.series.length; i++) {
          this.salesbyTeamLineData.datasets.push({
            label: this.salesbyTeamLine.series[i],
            data: this.salesbyTeamLine.data[i],
            fill: false,
            borderColor: this.colors[i]
          })
      }

      this.salesbyTeamLineChart.refresh()
      this.spiner["salesbyTeamLine"] = false
    })
  }

  onExportReports(){
    this.workbook = {
      fileName : "Ventas_" +
      this.project.name+ "_" +
      this.city+ "_" +
      this.team+ "_" +
      moment(this.startDate).format(this.timeformat) + "_al_" +
      moment(this.endDate).format(this.timeformat),
      sheets: []
    }
    this.onGenerateGlobal()
    this.onGenerateTopProduct()
    this.onGenerateTopProductStore()
    this.onGenerateTopSalesStore()
    this.onGenerateTopSalesProduct()
    this.onGenerateSalesbyChainDate()
    this.excelService.exportAsExcelFile(this.workbook)
  }

  onGenerateGlobal(){
    this.workbook.sheets.push({
      title: "Ventas",
      data: [
        {
          "Venta Total":this.totalSale,
          "Productos": this.totalProducts,
          "Piezas": this.totalPieces,
          "Impactos":this.totalImpacts
        }
      ]
    })
  }

  onGenerateTopProduct(){
    if(this.topSalesProducts){
      var sheet = {
        title: "Productos Vendidos",
        data: []
      }
      for (let i = 0; i < this.topSalesProducts.length; i++) {
        sheet.data.push(
          {
            "Código": this.topSalesProducts[i].code,
            "Nombre": this.topSalesProducts[i].name,
            "Categoría": this.topSalesProducts[i].category,
            "Cantidad": this.topSalesProducts[i].pieces,
            "Total": this.topSalesProducts[i].amount
          }
        )
      }
      this.workbook.sheets.push(sheet)
    }
  }

  onGenerateTopSalesProduct(){
    if(this.topSalesProducts){
      var sheet = {
        title: "Productos vs Monto Ventas",
        data: []
      }
      for (let i = 0; i < this.topSalesProducts.length; i++) {
        sheet.data.push(
          {
            "Código": this.topSalesProducts[i].code,
            "Producto": this.topSalesProducts[i].name,
            "Ventas": this.topSalesProducts[i].amount
          }
        )
      }
      this.workbook.sheets.push(sheet)
    }
  }

  onGenerateTopProductStore(){
    if(this.topSalesProductsStore){
      var sheet = {
        title: "Productos Vendidos Sucursal",
        data: []
      }
      for (let i = 0; i < this.topSalesProductsStore.length; i++) {
        sheet.data.push(
          {
            "Código": this.topSalesProductsStore[i].code,
            "Nombre": this.topSalesProductsStore[i].name,
            "Sucursal": this.topSalesProductsStore[i].store,
            "Cadena": this.topSalesProductsStore[i].chain,
            "Cantidad": this.topSalesProductsStore[i].pieces,
            "Total": this.topSalesProductsStore[i].amount
          }
        )
      }
      this.workbook.sheets.push(sheet)
    }
  }

  onGenerateTopSalesStore(){
    if(this.topSalesStore){
      var sheet = {
        title: "Prod vs Cant Vendida por Suc",
        data: []
      }
      for (let i = 0; i < this.topSalesStore.length; i++) {
        sheet.data.push(
          {
            "Sucursal": this.topSalesStore[i].store,
            "Cantidad": this.topSalesStore[i].pieces,
            "Ventas": this.topSalesStore[i].amount
          }
        )
      }
      this.workbook.sheets.push(sheet)
    }
  }

  onGenerateSalesbyChainDate(){
    if(this.salesbyTeamLine){
      var sheet = {
        title: "Ventas por Producto por Dia",
        data: []
      }
      for (let i = 0; i < this.salesbyTeamLine.data.length; i++) {
          var obj = {}
          obj["Código"] = this.salesbyTeamLine.series[i]
          for (let j = 0; j < this.salesbyTeamLine.labels.length; j++) {
            obj[this.salesbyTeamLine.labels[j]] = this.salesbyTeamLine.data[i][j]
          }
          sheet.data.push( obj )
      }
      this.workbook.sheets.push(sheet)
    }
  }



}
