import { Component, OnInit, HostListener } from '@angular/core';
import { DataService } from '../../../services/data.service';

@Component({
  selector: 'app-menu-sidebar',
  templateUrl: './menu-sidebar.component.html',
})
export class MenuSidebarComponent implements OnInit {

  navigationToggled = false;
  navigationOpened = false;

  constructor(
    private dataService: DataService
  ) { }

  ngOnInit() {
    this.dataService.navigationToggledEmiter.subscribe((value)=>{
      this.navigationToggled = value;
    });
    this.navigationToggled = this.dataService.navigationToggled;

    this.dataService.navigationOpenedEmiter.subscribe((value)=>{
      this.navigationOpened = value;
    });
    this.navigationOpened = this.dataService.navigationOpened;
  }

  @HostListener('window:resize', ['$event'])
  onResize() {
    this.dataService.showLeftNavigation();
  }

  toggleTopNavigation(){
    this.dataService.toggleTopNavigation();
  }

}
