  onExportReports() {
    this.workbook = {
      fileName: "Ventas_" +
        this.project.name + "_" +
        moment(this.startDate).format(this.timeformat) + "_al_" +
        moment(this.endDate).format(this.timeformat),
      sheets: []
    }
    this.onGenerateGlobal()
    this.onGenerateSalesbyCity()
    this.onGenerateSalesbyChain()
    this.onGenerateTopSalesProduct()
    this.onGenerateTopSalesStore()
    this.onGenerateSalesbyChainDate()
    this.excelService.exportAsExcelFile(this.workbook)
  }

  onGenerateGlobal() {
    if (this.totalSales) {
      this.workbook.sheets.push({
        title: "Ventas",
        data: [
          {
            "Venta Total": this.totalSales,
            "Productos": this.totalProducts,
            "Piezas": this.totalPieces,
            "Cadenas": this.totalChains
          }
        ]
      })
    }
  }

  onGenerateSalesbyCity() {
    if (this.salesbyCityData) {
      var sheet = {
        title: "Ventas por Ciudad",
        data: []
      }
      for (let i = 0; i < this.salesbyCityData.labels.length; i++) {
        sheet.data.push({
          "Ciudad": this.salesbyCityData.labels[i],
          "Ventas": this.salesbyCityData.datasets[0].data[i]
        })
      }
      this.workbook.sheets.push(sheet)
    }
  }

  onGenerateSalesbyChain() {
    if (this.salesbyChain) {
      var sheet = {
        title: "Ventas por Cadena",
        data: []
      }
      for (let i = 0; i < this.salesbyChain.length; i++) {
        sheet.data.push(
          {
            "Cadena": this.salesbyChain[i].chain_name,
            "Ventas": this.salesbyChain[i].sale
          }
        )
      }
      this.workbook.sheets.push(sheet)
    }
  }

  onGenerateTopSalesProduct() {
    if (this.topSalesProduct) {
      var sheet = {
        title: "Productos Vendidos",
        data: []
      }
      for (let i = 0; i < this.topSalesProduct.length; i++) {
        sheet.data.push(
          {
            "Codigo": this.topSalesProduct[i].code,
            "Nombre": this.topSalesProduct[i].name,
            "Categoría": this.topSalesProduct[i].category,
            "Cantidad": this.topSalesProduct[i].pieces,
            "Total": this.topSalesProduct[i].amount
          }
        )
      }
      this.workbook.sheets.push(sheet)
    }
  }

  onGenerateTopSalesStore() {
    if (this.topSalesStore) {
      var sheet = {
        title: "Ventas por Sucursal",
        data: []
      }
      for (let i = 0; i < this.topSalesStore.length; i++) {
        sheet.data.push(
          {
            "Sucursal": this.topSalesStore[i].chain_name,
            "Cadena": this.topSalesStore[i].subsidiary,
            "Total": this.topSalesStore[i].amount
          }
        )
      }
      this.workbook.sheets.push(sheet)
    }
  }

  onGenerateSalesbyChainDate() {
    if (this.salesbyChainDate) {
      var sheet = {
        title: "Ventas por Cadena por Fecha",
        data: []
      }
      for (let i = 0; i < this.salesbyChainDate.data.length; i++) {
        var obj = {}
        obj["Cadena"] = this.salesbyChainDate.series[i]
        for (let j = 0; j < this.salesbyChainDate.labels.length; j++) {
          obj[this.salesbyChainDate.labels[j]] = this.salesbyChainDate.data[i][j]
        }
        sheet.data.push(obj)
      }
      this.workbook.sheets.push(sheet)
    }
  }


