import { Component, OnInit } from '@angular/core';
import { DataService } from '@services/data.service';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
})
export class UserListComponent implements OnInit {

  users:any;
  user:any;
  display: boolean = false;
  filter: String

  constructor(
    private dataService: DataService,
  ) { }

  ngOnInit() {
    this.onFindUsers();
  }

  onFindUsers(){
    this.users  = this.dataService.find("users");
  }

  onShowDeleteDialog(user) {
    this.user = user;
    this.display = true;
  }

  onDelete(){
    this.dataService.deleteOne("users", this.user.doc._id + "/" + this.user.doc._rev).subscribe(() => {
      this.onFindUsers();
      this.display=false;
    });
  }

}
