import { Component, OnInit, Input } from '@angular/core';
import { DataService } from '@services/data.service';

@Component({
  selector: 'app-project-chains',
  templateUrl: './project-chains.component.html',
})
export class ProjectChainsComponent implements OnInit {

  @Input("projectId") projectId: any;

  chains: Array<any>;
  chain: any;

  displayDeleteDialog: Boolean;
  displayChainDialog: Boolean;
  filter:String

  constructor(
    private dataService:DataService
  ) { }

  ngOnInit() {
    this.onFindChains();
  }

  onFindChains(){
    this.dataService.findById("chain", this.projectId).subscribe((data)=>{
      this.chains = data;
    })
  }

  onShowDeleteDialog(chain){
    this.chain = chain;
    this.displayDeleteDialog = true;
  }

  onEdit(chain){
    this.chain = chain;
    this.displayChainDialog = true;
  }

  onNew(){
    this.chain = {};
    this.chain.coordinates = {};
    this.displayChainDialog = true;
  }

  onCancel(){
    this.chain = {};
    this.displayChainDialog = false;
  }

  onSave(){
    this.chain.type="chain";
    this.chain.project=this.projectId;
    this.dataService.insertOne("chain", { chain: this.chain, id: this.projectId }).subscribe((data)=>{
      console.log(data);
      this.displayChainDialog = false;
      this.onFindChains();
    });
  }

  onDelete(){
    this.dataService.deleteOne("chain", this.chain._id + "/" + this.chain._rev).subscribe((data)=>{
      console.log(data);
      this.displayDeleteDialog = false;
      this.onFindChains();
    });
  }

}
