import { Component, OnInit, Input } from '@angular/core';
import { DataService } from '@services/data.service';

@Component({
  selector: 'app-project-products',
  templateUrl: './project-products.component.html',
})
export class ProjectProductsComponent implements OnInit {

  @Input("projectId") projectId: any;

  products: Array<any>;
  product: any;

  displayDeleteDialog: Boolean;
  displayProductDialog: Boolean;

  filter: String

  constructor(
    private dataService:DataService
  ) { }

  ngOnInit() {
    this.onFindProducts();
  }

  onFindProducts(){
    this.dataService.findById("product", this.projectId).subscribe((data)=>{
      console.log(data)
      this.products = data.rows;
    })
  }

  onShowDeleteDialog(product){
    this.product = product;
    this.displayDeleteDialog = true;
  }

  onEdit(product){
    this.product = product;
    this.displayProductDialog = true;
  }



  onNew(){
    this.product = {};
    this.displayProductDialog = true;
  }

  onCancel(){
    this.product = {};
    this.displayProductDialog = false;
  }

  onSave(){
    this.product.type="product";
    this.product.project=this.projectId;
    this.dataService.insertOne("product", { product: this.product, id: this.projectId }).subscribe((data)=>{
      console.log(data);
      this.displayProductDialog = false;
      this.onFindProducts();
    });
  }

  onDelete(){
    this.dataService.deleteOne("product", this.product._id + "/" + this.product._rev).subscribe((data)=>{
      console.log(data);
      this.displayDeleteDialog = false;
      this.onFindProducts();
    });
  }

}
