import { Component, OnInit, ViewChild } from '@angular/core';
import { DataService }        from '@services/data.service';
import { ExcelService } from '@services/excel.service';
import { SecurityService } from '@services/security.service';

import * as _ from 'lodash';
import * as moment from 'moment';
import { extendMoment } from 'moment-range';

const { range } = extendMoment(moment);


@Component({
  selector: 'app-impacts-projects',
  templateUrl: './impacts-projects.component.html',
})
export class ImpactsProjectsComponent implements OnInit {

  @ViewChild('impactbyCityChart', { static: true }) impactbyCityChart;
  @ViewChild('impactsVsSalesByChainChart', { static: true }) impactsVsSalesByChainChart;
  @ViewChild('impactsVsSalesVsStockByDateChart', { static: true }) impactsVsSalesVsStockByDateChart;

  colors = ['#4bc0c0', '#ec7063', '#b078c7', '#73c6b6', '#73c6b6', '#f6b7b0', '#f9e89b', '#808b96', '#885656'];
  spiner = {
    impactbyCity:false,
    impactsVsSalesByChain:false,
    teamImpactsbyStore:false,
    impactsbyStore:false,
    impactsVsSalesVsStockByDate:false
  };

  cityChartExpand
  impactsVsSalesByChainExpand
  topSalesProductExpand
  topSalesStoreExpand
  salesbyChainChartExpand

  projects:any;
  project:any;
  startDate:any = new Date();
  endDate:any = new Date();

  totalImpacts:any =0;
  totalSales:any =0;
  totalPieces:any =0;
  totalStock:any =0;

  impactbyCityData:any;
  impactVsSalesbyChain:any;

  impactsVsSalesByChainData:any;
  impactsVsSalesByChainOptions:any;

  teamImpactsbyStore:any;
  impactsbyStore:any;

  impactsVsSalesVsStockByDateData:any;
  impactsVsSalesVsStockByDateOptions:any;

  workbook:any;
  timeformat = "YYYY-MM-DD";

  constructor(
    private dataService: DataService,
    private excelService: ExcelService,
    private securityService: SecurityService
  ) {


    this.impactbyCityData = {
      labels: [],
      datasets: [{
        data: [],
        backgroundColor: [ '#3366CC88', '#DC391288', '#FF990088', '#10961888', '#99009988',
'#3B3EAC88', '#0099C688', '#DD447788', '#66AA0088', '#B82E2E88',
'#31639588', '#99449988', '#22AA9988', '#AAAA1188', '#6633CC88',
'#E6730088', '#8B070788', '#32926288', '#5574A688', '#3B3EAC88'],
        hoverBackgroundColor: [ '#3366CC88', '#DC391288', '#FF990088', '#10961888', '#99009988',
'#3B3EAC88', '#0099C688', '#DD447788', '#66AA0088', '#B82E2E88',
'#31639588', '#99449988', '#22AA9988', '#AAAA1188', '#6633CC88',
'#E6730088', '#8B070788', '#32926288', '#5574A688', '#3B3EAC88']
      }]
    };

    this.impactsVsSalesByChainData = {
      labels: [],
      datasets: [{
        label: 'Impactos',
        backgroundColor: '#ffa300',
        borderColor: '#fff',
        borderWidth: 1,
        pointRadius: 10,
        showLine: false,
        pointStyle: 'rectRot',
        type: 'line',
        yAxisID: 'A',
        data: []
      },
      {
        label: 'Ventas',
        borderWidth: 1,
        type: 'bar',
        backgroundColor: '#f1336788',
        borderColor: '#bf1b48',
        yAxisID: 'B',
        data: []
      }]
    };

    this.impactsVsSalesByChainOptions ={
      tooltips: {
         callbacks: {
            label: function(t, d) {
               var xLabel = d.datasets[t.datasetIndex].label;
               var value  = t.yLabel.toFixed(2);
               var yLabel = value >= 1000 ? value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : value;
               return xLabel + ': ' + yLabel;
            }
         }
      },
      scales: {
        yAxes: [{
          id: 'A',
          type: 'linear',
          position: 'left',
        }, {
          id: 'B',
          type: 'linear',
          position: 'right',
        }]
      }
    };

    this.impactsVsSalesVsStockByDateData = {
      labels: [],
      datasets: [{
        label: 'Existencia',
        backgroundColor: '#e4e24c',
        borderColor: '#fff',
        borderWidth: 1,
        pointRadius: 10,
        showLine: false,
        pointStyle: 'rectRot',
        type: 'line',
        yAxisID: 'A',
        data: []
      },
      {
        label: 'Impactos',
        borderWidth: 1,
        type: 'line',
        fill: false,
        backgroundColor: '#71b740',
        borderColor: '#fff',
        yAxisID: 'A',
        data: []
      },
      {
        label: 'Ventas',
        backgroundColor: '#ca0d0d88',
        borderColor: '#900b0b',
        borderWidth: 1,
        type: 'bar',
        yAxisID: 'B',
        data: []
      }]
    };

    this.impactsVsSalesVsStockByDateOptions ={
      tooltips: {
         callbacks: {
            label: function(t, d) {
               var xLabel = d.datasets[t.datasetIndex].label;
               var value  = t.yLabel.toFixed(2);
               var yLabel = value >= 1000 ? value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") : value;
               return xLabel + ': ' + yLabel;
            }
         }
      },
      scales: {
        yAxes: [{
          id: 'A',
          type: 'linear',
          position: 'left',
        },{
          id: 'B',
          type: 'linear',
          position: 'right',
        }]
      }
    };
}

  ngOnInit() {
    var user = this.securityService.getCurrentUser()
    if(this.securityService.hasPermisions(['admin'])){
      this.onFindProyects();
    }else{
      this.onFindProyectsByUser(user._id)
    }
    this.startDate = this.dataService.filters.startDate;
    this.endDate = this.dataService.filters.endDate;
  }

  onChangeStartDate(){
    this.dataService.filters.startDate = this.startDate
  }

  onChangeEndDate(){
    this.dataService.filters.endDate = this.endDate
  }

  onChangeProject(){
    this.dataService.filters.project = this.project
  }

  onCompareById(item1, item2) {
    if(item1 && item2){
      return item1._id === item2._id;
    }
    else{
      return false
    }
  }

  onFind(){
    this.onFindTotalImpacts();
    this.onFindTotalSales();
    this.onFindTotalPieces();
    this.onFindTotalStockbyProject();
    this.onFindImpactsbyCity();
    this.onFindImpactsVsSalesByChain();
    this.onFindTeamImpactsbyStore();
    this.onFindImpactsbyStore();
    this.onFindImpactsVsSalesVsStockByDate();
  }

  onFindProyects(){
    this.dataService.find("project").subscribe((data) => {
      this.projects = [];
      data.rows.forEach(function(element) {
        this.projects.push(element.doc);
      }, this);
      this.project = this.dataService.filters.project || this.projects[0] || {} ;
    });
  }

  onFindProyectsByUser(userId){
    this.dataService.find("users/"+userId+"/projects").subscribe((data) => {
      this.projects = data.docs;
      this.project = this.projects[0];
    });
  }

  onFindTotalImpacts(){
    this.dataService.findBy("project", this.project._id +"/impact/total/"+this.startDate+"/"+this.endDate).subscribe((data) => {
      this.totalImpacts = data.totalimpacts;
    });
  }

  onFindTotalSales(){
    this.dataService.findBy("sales/project", this.project._id +"/totalSales/"+this.startDate+"/"+this.endDate).subscribe((data) => {
      this.totalSales = data.totalsales;
    });
  }

  onFindTotalPieces(){
    this.dataService.findBy("sales/project", this.project._id +"/totalPieces/"+this.startDate+"/"+this.endDate).subscribe((data) => {
      this.totalPieces = data.totalpieces;
    });
  }

  onFindTotalStockbyProject(){
    this.dataService.findBy("project", this.project._id +"/inventory/stock/total/"+this.startDate+"/"+this.endDate).subscribe((data) => {
      this.totalStock = data.totalstock;
    });
  }

  onFindImpactsbyCity(){
    this.spiner["impactbyCity"] =true;
    this.dataService.findBy("project", this.project._id +"/impact/bycity/"+this.startDate+"/"+this.endDate).subscribe((data) => {
      this.impactbyCityData.labels = data.impactsbycity.labels;
      this.impactbyCityData.datasets[0].data = data.impactsbycity.values;
      this.impactbyCityChart.refresh();
      this.spiner["impactbyCity"] =false;
    });
  }

  onFindImpactsVsSalesByChain(){
    this.spiner['impactsVsSalesByChain'] = true;
    let promises  = [];
    promises.push(new Promise((resolve, reject) => {
      this.dataService.findBy("project", this.project._id +"/impact/bychain/"+this.startDate+"/"+this.endDate).subscribe((data) => {
        resolve(data.impactsbychain);
      }, (err) =>{
        reject(err);
      });
    }));

    promises.push( new Promise((resolve, reject) => {
      this.dataService.findBy("sales/project", this.project._id +"/salesbychain/"+this.startDate+"/"+this.endDate).subscribe((data) => {
        resolve(data.salesbychain);
      }, (err) =>{
        reject(err);
      });
    }));

    Promise.all(promises).then(values => {

      this.impactsVsSalesByChainData.labels = [];
      this.impactsVsSalesByChainData.datasets[0].data = [];
      this.impactsVsSalesByChainData.datasets[1].data = [];

      this.impactVsSalesbyChain = _.merge(values[0], values[1]);
      for (let i = 0; i < this.impactVsSalesbyChain.length; i++) {
        this.impactsVsSalesByChainData.labels.push(this.dataService.formatLabel(this.impactVsSalesbyChain[i].chain_name, 10));
        this.impactsVsSalesByChainData.datasets[0].data.push(this.impactVsSalesbyChain[i].impacts);
        this.impactsVsSalesByChainData.datasets[1].data.push(this.impactVsSalesbyChain[i].sale);
      }
      this.impactsVsSalesByChainChart.refresh();
      this.spiner['impactsVsSalesByChain'] = false;

    });
  }

  onFindTeamImpactsbyStore(){
    this.spiner["teamImpactsbyStore"] = true;
    this.dataService.findBy("project", this.project._id +"/team/impact/bystore/"+this.startDate+"/"+this.endDate).subscribe((data) => {
      this.teamImpactsbyStore = data;
      this.spiner["teamImpactsbyStore"] = false;
    });
  }

  onFindImpactsbyStore(){
    this.spiner["impactsbyStore"] = true;
    this.dataService.findBy("project", this.project._id +"/impact/bystore/"+this.startDate+"/"+this.endDate).subscribe((data) => {
      this.impactsbyStore = data.impactsbystore;
      this.spiner["impactsbyStore"] = false;
    });
  }

  onFindImpactsVsSalesVsStockByDate(){
    this.spiner['impactsVsSalesVsStockByDate'] = true;
    var frequency = moment(this.startDate).isSame(this.endDate) ? "hourly" : "daily";
    var startTime = moment(this.startDate).startOf("day");
    var endTime  = moment(this.endDate).endOf("day");
    var timeformat = "";
    var hours;
    var dr = range(startTime,endTime);

    if(frequency == "daily"){
      timeformat = "YYYY-MM-DD";
      hours = Array.from(dr.by("day"));
    } else {
      timeformat = "YYYY-MM-DD HH:mm";
      hours = Array.from(dr.by("hours"));
    }
    var labels = [];

    hours.map(m => {
      console.log()
      labels.push(m.format(timeformat));
    });

    let promises  = [];
    //project/project:landsteiner-contigo/inventory/stock/daily/2016-11-20T00:00:00-07:00/2016-12-20T23:59:59-07:00
    //project/project:landsteiner-contigo/inventory/stock/daily/Sat%20Oct%2001%202016%2000:00:00%20GMT-0600%20(Mexican%20Pacific%20Daylight%20Time)/Sat%20Nov%2026%202016%2000:00:00%20GMT-0700%20(Mexican%20Pacific%20Standard%20Time)
    promises.push(new Promise((resolve, reject) => {
      this.dataService.findBy("project", this.project._id +"/inventory/stock/"+ frequency + "/" + this.startDate+"/"+this.endDate).subscribe((data) => {
        resolve(data);
      }, (err) =>{
        reject(err);
      });
    }));

    promises.push( new Promise((resolve, reject) => {
      this.dataService.findBy("project", this.project._id +"/impact/"+ frequency + "/" +this.startDate+"/"+this.endDate).subscribe((data) => {
        resolve(data);
      }, (err) =>{
        reject(err);
      });
    }));

    promises.push( new Promise((resolve, reject) => {
      this.dataService.findBy("project", this.project._id +"/sales/"+ frequency + "/" +this.startDate+"/"+this.endDate).subscribe((data) => {
        resolve(data);
      }, (err) =>{
        reject(err);
      });
    }));

    Promise.all(promises).then(values => {
      console.log(values);

      var data = [
          _.fill(Array(labels.length),0),
          _.fill(Array(labels.length),0),
          _.fill(Array(labels.length),0)
        ];

      for (let i = 0; i < labels.length; i++) {
        var stock_results = _.filter(values[0], function(o) {
          var timearray = [o.key[1],o.key[2],o.key[3]];
          if(frequency == "hourly")  timearray.push(o.key[4]);
          return o.value !== 0 && moment(timearray).format(timeformat) === labels[i];
        });
        if(stock_results !== null && stock_results.length === 1){
          data[0][i] = stock_results[0].value;
        }
        var impacts_results = _.filter(values[1], function(o) {
          var timearray = [o.key[1],o.key[2],o.key[3]];
          if(frequency == "hourly")  timearray.push(o.key[4]);
          return o.value !== 0 && moment(timearray).format(timeformat) === labels[i];
        });
        if(impacts_results !== null && impacts_results.length === 1){
          data[1][i] = impacts_results[0].value;
        }
        var sales_results = _.filter(values[2], function(o) {
          var timearray = [o.key[1],o.key[2],o.key[3]];
          if(frequency == "hourly")  timearray.push(o.key[4]);
          return o.value !== 0 && moment(timearray).format(timeformat) === labels[i];
        });
        if(sales_results !== null && sales_results.length === 1){
          data[2][i] = sales_results[0].value;
        }
      }
      console.log(data)

      this.impactsVsSalesVsStockByDateData.labels = labels;
      this.impactsVsSalesVsStockByDateData.datasets[0].data = data[0];
      this.impactsVsSalesVsStockByDateData.datasets[1].data = data[1];
      this.impactsVsSalesVsStockByDateData.datasets[2].data = data[2];

      this.impactsVsSalesVsStockByDateChart.refresh();
      this.spiner['impactsVsSalesVsStockByDate'] = false;
    });
  }

  onExportReports(){
    this.workbook = {
      fileName : "Impactos_" +
        this.project.name+ "_" +
        moment(this.startDate).format(this.timeformat) + "_al_" +
        moment(this.endDate).format(this.timeformat),
      sheets: []
    };
    this.onGenerateGlobal();
    this.onGenerateImpactbyCityData();
    this.onGenerateImpactsVsSalesByChain();
    this.onGenerateTeamImpactsbyStore();
    this.onGenerateImpactsbyStore();
    this.onGenerateSalesbyChainDate();
    this.excelService.exportAsExcelFile(this.workbook);
  }

  onGenerateGlobal(){
    if(this.totalSales){
      this.workbook.sheets.push({
        title: "Impactos",
        data: [
          {
            "Impactos":this.totalImpacts,
            "Ventas": this.totalSales,
            "Piezas": this.totalPieces,
            "Existencia":this.totalStock
          }
        ]
      });
    }
  }

  onGenerateImpactbyCityData(){
    if(this.impactbyCityData){
      var sheet = {
        title: "Impactos por Ciudad",
        data: []
      };
      for (let i = 0; i < this.impactbyCityData.labels.length; i++) {
        sheet.data.push({
          "Ciudad": this.impactbyCityData.labels[i],
          "Ventas": this.impactbyCityData.datasets[0].data[i]
        });
      }
      this.workbook.sheets.push(sheet);
    }
  }

  onGenerateImpactsVsSalesByChain(){
    if(this.impactVsSalesbyChain){
      var sheet = {
        title: "Impactos vs Ventas por Cadena",
        data: []
      };
      for (let i = 0; i < this.impactVsSalesbyChain.length; i++) {
        sheet.data.push({
          "Cadena": this.impactVsSalesbyChain[i].chain_name,
          "Impactos": this.impactVsSalesbyChain[i].impacts,
          "Ventas": this.impactVsSalesbyChain[i].sale
        });
      }
      this.workbook.sheets.push(sheet);
    }
  }

  onGenerateTeamImpactsbyStore(){
    if(this.teamImpactsbyStore){
      var sheet = {
        title: "Equipos con más Impactos",
        data: []
      };
      for (let i = 0; i < this.teamImpactsbyStore.length; i++) {
        sheet.data.push(
          {
            "Equipo": this.teamImpactsbyStore[i].team_name,
            "Sucursal": this.teamImpactsbyStore[i].store_name,
            "Cadena": this.teamImpactsbyStore[i].chain_name,
            "Impactos": this.teamImpactsbyStore[i].impacts
          }
        )
      }
      this.workbook.sheets.push(sheet);
    }
  }

  onGenerateImpactsbyStore(){
    if(this.impactsbyStore){
      var sheet = {
        title: "Tiendas con más Ventas",
        data: []
      };
      for (let i = 0; i < this.impactsbyStore.length; i++) {
        sheet.data.push(
          {
            "Sucursal": this.impactsbyStore[i].store_name,
            "Cadena": this.impactsbyStore[i].chain_name,
            "Total": this.impactsbyStore[i].impacts
          }
        )
      }
      this.workbook.sheets.push(sheet);
    }
  }

  onGenerateSalesbyChainDate(){
    if(this.impactsVsSalesVsStockByDateData){
      var sheet = {
        title: "Ventas por Cadena por Fecha",
        data: []
      };
      for (let i = 0; i < this.impactsVsSalesVsStockByDateData.datasets.length; i++) {
          var obj = {};
          obj["Tipo"] = this.impactsVsSalesVsStockByDateData.datasets[i].label
          for (let j = 0; j < this.impactsVsSalesVsStockByDateData.datasets[i].data.length; j++) {
            obj[this.impactsVsSalesVsStockByDateData.labels[j]] = this.impactsVsSalesVsStockByDateData.datasets[i].data[j];
          }
          sheet.data.push( obj );
      }
      this.workbook.sheets.push(sheet);
    }
  }

}
