import { Component, OnInit } from '@angular/core'
import { DataService }        from '@services/data.service'
import { SecurityService } from '@services/security.service'
import { MessageService } from 'primeng/api';

import * as _ from 'lodash'

@Component({
  selector: 'app-tickets',
  templateUrl: './tickets.component.html',
})
export class TicketsComponent implements OnInit {

  projects:Array<any>
  project:any
  chains:Array<any>
  chain:any
  stores:Array<any>
  store:any
  tickets:any
  documents:Array<any>
  spiner:boolean = false

  ticket:any
  displayEdit:boolean = false
  display:boolean = false
  photos: Array<any>

  startDate:any = new Date()
  endDate:any = new Date()

  constructor(
    private dataService: DataService,
    private securityService: SecurityService,
    private messageService:MessageService
  ) { }

  ngOnInit() {
    var user = this.securityService.getCurrentUser()
    if(this.securityService.hasPermisions(['admin'])){
      this.onFindProyects()
    }else{
      this.onFindProyectsByUser(user._id)
    }
  }

  onFindProyects(){
    this.dataService.find("project").subscribe((data) => {
      var projects = []
      data.rows.forEach(function(element) {
        projects.push(element.doc)
      })
      this.projects = projects
      this.project = this.projects[0] || {}
      this.onFindChains()
    })
  }

  onFindProyectsByUser(userId){
    this.dataService.find("users/"+userId+"/projects").subscribe((data) => {
      this.projects = data.docs
      this.project = this.projects[0] || {}
      this.onFindChains()
    })
  }

  onChangeProject(){
    this.onFindChains()
  }

  onChangeChain(){
    this.onFindStores()
  }

  onFindChains(){
    console.log("onFindChains")
    this.dataService.findById("chain", this.project._id).subscribe((data) => {
      console.log(data)
      this.chains = data
      this.chain = this.chains[0] || ""
      this.onFindStores()
    })
  }

  onFindStores(){
    console.log("onFindStores", this.chain.name)
    this.dataService.findBy("stores/chain", this.chain.name).subscribe((data) => {
      this.stores = data
      this.store = this.stores[0] || ""
    })
  }

  onFind(){
    console.log("onFind")
    this.displayEdit = false
    this.onFindTicketsperProject()
  }

  onFindTicketsperProject(){
    console.log("onFindTicketsperProject")

    this.spiner = true
    this.dataService.findBy("tickets/project", this.project._id + '/chain/' + this.chain.name + "/store/" + this.store.doc.name +'/'+this.startDate+'/'+this.endDate).subscribe((data) => {
      this.documents = data
      console.log(this.documents)
      this.spiner = false
    })
  }

  onSelect(ticket){
    console.log(ticket)
    this.ticket = ticket
    this.displayEdit = true
  }

  onSave(){
    this.getTotals()
    this.dataService.insertOne("sales", {"ticket":this.ticket.doc}).subscribe(()=>{
      console.log("ticket Saved", this.ticket)
      this.messageService.add({severity:'success', summary:'', detail:'Usuario guardado'})
      this.displayEdit = false
      this.onFind()
    })
  }

  getTotals(){
    this.ticket.doc.total_sale = 0
    this.ticket.doc.total_pieces = 0
    this.ticket.doc.total_products = 0
    if(this.ticket && this.ticket.doc){
      this.ticket.doc.item.forEach((element:any) => {
        if(element.quantity && element.quantity != 0 && element.price && element.price != 0){
          this.ticket.doc.total_sale += (element.quantity * element.price)
          this.ticket.doc.total_pieces = element.quantity + parseInt(this.ticket.doc.total_pieces)
          this.ticket.doc.total_products++
        }
      })
    }
  }
  onShowPhotos(photos){
    console.log(photos)
    this.photos = []
    for (let i = 0; i < photos.length; i++) {
      this.photos.push({source: photos[i], alt:'', title:''})
    }

    this.display = true
  }

}
