  onExportReports() {
    this.workbook = {
      fileName: "Ventas_Por_Producto_" +
        this.project.name + "_" +
        moment(this.startDate).format(this.timeformat) + "_al_" +
        moment(this.endDate).format(this.timeformat),
      sheets: []
    };
    this.onGenerateGlobal();
    this.onGenerateTopSalesProduct();
    this.onGenerateTopSalesStore();
    this.onGenerateSalesbyChainDate();
    this.excelService.exportAsExcelFile(this.workbook);
  }

  onGenerateGlobal() {
    if (this.totalSales) {
      this.workbook.sheets.push({
        title: "Ventas",
        data: [
          {
            "Venta Total": this.totalSales,
            "Productos Top": this.totalProducts,
            "Piezas": this.totalPieces,
            "Sucursales": this.totalStores
          }
        ]
      });
    }
  }

  onGenerateTopSalesProduct() {
    if (this.topSalesProduct) {
      var sheet = {
        title: "Productos Vendidos",
        data: []
      };
      for (let i = 0; i < this.topSalesProduct.length; i++) {
        sheet.data.push(
          {
            "Código": this.topSalesProduct[i].code,
            "Nombre": this.topSalesProduct[i].name,
            "Cantidad": this.topSalesProduct[i].pieces,
            "Total": this.topSalesProduct[i].amount
          }
        )
      }
      this.workbook.sheets.push(sheet);
    }
  }

  onGenerateTopSalesStore() {
    if (this.topTenStores) {
      var sheet = {
        title: "Sucursales Venta Productos",
        data: []
      };
      for (let i = 0; i < this.topTenStores.length; i++) {
        sheet.data.push(
          {
            "Sucursal": this.topTenStores[i].store,
            "Cadena": this.topTenStores[i].chain_name,
            "Total": this.topTenStores[i].amount
          }
        )
      }
      this.workbook.sheets.push(sheet);
    }
  }

  onGenerateSalesbyChainDate() {
    if (this.salesbyChainLine) {
      var sheet = {
        title: "Ventas por Producto",
        data: []
      };
      for (let i = 0; i < this.salesbyChainLine.data.length; i++) {
        var obj = {};
        obj["Código"] = this.salesbyChainLine.series[i]
        for (let j = 0; j < this.salesbyChainLine.labels.length; j++) {
          obj[this.salesbyChainLine.labels[j]] = this.salesbyChainLine.data[i][j];
        }
        sheet.data.push(obj);
      }
      this.workbook.sheets.push(sheet);
    }
  }





