import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SecurityGuard } from './security.guard';

import { MainComponent } from './layout/main/main.component';
import { CleanComponent } from './layout/clean/clean.component';

//import { HomeComponent } from './components/home/home.component';
import { SalesComponent } from './components/sales/sales.component';
import { SalesProjectsComponent } from './components/sales/projects/projects.component';
import { ChainComponent } from './components/sales/chain/chain.component';
import { ProductsComponent } from './components/sales/products/products.component';
import { TeamsComponent } from './components/sales/teams/teams.component';
import { ImpactsComponent } from './components/impacts/impacts.component';
import { TicketsComponent } from './components/tickets/tickets.component';
import { EvidencesComponent } from './components/evidences/evidences.component';
import { IncidenceComponent } from './components/incidence/incidence.component';
import { AttendanceComponent } from './components/attendance/attendance.component';
import { ProjectsComponent } from './components/administration/projects/projects.component';
import { ImpactsProjectsComponent } from './components/impacts/impacts-projects/impacts-projects.component';
import { ImpactsChainsComponent } from './components/impacts/impacts-chains/impacts-chains.component';
import { AdministrationComponent } from './components/administration/administration.component';
import { UsersComponent } from './components/administration/users/users.component';
import { LoginComponent } from './components/login/login.component';
import { UserListComponent } from './components/administration/users/user-list/user-list.component';
import { UserComponent } from './components/administration/users/user/user.component';
import { LogoutComponent } from './components/logout/logout.component';

import { ProjectListComponent } from './components/administration/projects/project-list/project-list.component';
import { ProjectComponent } from './components/administration/projects/project/project.component';
import { InventoryComponent } from './components/inventories/inventory/inventory.component';
import { InventoryProductsComponent } from './components/inventories/inventory-products/inventory-products.component';
import { InventoriesComponent } from './components/inventories/inventories.component';


const routes: Routes = [
  { path: '',   redirectTo: 'sales', pathMatch: 'full' },
  { path: '', component: CleanComponent, children:[
    { path: 'login', component: LoginComponent },
    { path: 'logout', component: LogoutComponent },
  ] },
  { path: '', component: MainComponent, children:[
    { path: 'home', redirectTo: 'sales', canActivate : [SecurityGuard] },
    { path: 'sales', component: SalesComponent, canActivate : [SecurityGuard], children:[
      { path: '', redirectTo: 'projects', pathMatch: 'full'},
      { path: 'projects', component: SalesProjectsComponent, canActivate : [SecurityGuard], data:{roles:['admin','cliente']} },
      { path: 'chain', component: ChainComponent, canActivate : [SecurityGuard], data:{roles:['admin','cliente']} },
      { path: 'products', component: ProductsComponent, canActivate : [SecurityGuard], data:{roles:['admin','cliente']} },
      { path: 'teams', component: TeamsComponent, canActivate : [SecurityGuard], data:{roles:['admin','cliente']} },
    ]},
    { path: 'impacts', component: ImpactsComponent, canActivate : [SecurityGuard], children:[
      { path: '', redirectTo: 'projects', pathMatch: 'full'},
      { path: 'projects', component: ImpactsProjectsComponent, canActivate : [SecurityGuard], data:{roles:['admin','cliente']} },
      { path: 'chain', component: ImpactsChainsComponent, canActivate : [SecurityGuard], data:{roles:['admin','cliente']} },
    ]},
    { path: 'tickets', component: TicketsComponent, canActivate : [SecurityGuard], data:{roles:['admin','cliente']} },
    { path: 'evidences', component: EvidencesComponent, canActivate : [SecurityGuard], data:{roles:['admin','cliente']} },
    { path: 'incidence', component: IncidenceComponent, canActivate : [SecurityGuard], data:{roles:['admin']} },
    { path: 'inventories', component: InventoriesComponent, canActivate : [SecurityGuard], children:[
      { path: '', redirectTo: 'inventory', pathMatch: 'full'},
      { path: 'inventory', component: InventoryComponent, canActivate : [SecurityGuard], data:{roles:['admin','cliente']} },
      { path: 'products', component: InventoryProductsComponent, canActivate : [SecurityGuard], data:{roles:['admin','cliente']} },
    ]},
    { path: 'attendance', component: AttendanceComponent , canActivate : [SecurityGuard], data:{roles:['admin']}},
    { path: 'admin', component: AdministrationComponent, canActivate : [SecurityGuard], data:{roles:['admin']}, children:[
      { path: '', redirectTo: 'projects', pathMatch: 'full'},
      { path: 'users', component: UsersComponent, canActivate : [SecurityGuard], children: [
        { path: '', redirectTo: 'list', pathMatch: 'full'},
        { path: 'list', component: UserListComponent, canActivate : [SecurityGuard], data:{roles:['admin']}},
        { path: 'new', component: UserComponent, canActivate : [SecurityGuard], data:{roles:['admin']}},
        { path: 'user/:id', component: UserComponent, canActivate : [SecurityGuard], data:{roles:['admin']}},
      ]},
      { path: 'projects', component: ProjectsComponent, canActivate:[SecurityGuard], children: [
        { path: '', redirectTo: 'list', pathMatch: 'full'},
        { path: 'list', component: ProjectListComponent, canActivate : [SecurityGuard], data:{roles:['admin']}},
        { path: 'new', component: ProjectComponent, canActivate : [SecurityGuard], data:{roles:['admin']}},
        { path: 'project/:id', component: ProjectComponent, canActivate : [SecurityGuard], data:{roles:['admin']}},
      ]},
    ]},
  ] },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
