import { Component, OnInit } from '@angular/core';
import { SecurityService } from '@services/security.service';
import { Router } from '@angular/router';
import { MessageService } from 'primeng/api';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html'
})
export class LoginComponent implements OnInit {

  credentials: any = {};

  constructor(
    private securityService: SecurityService,
    private router: Router,
    private messageService: MessageService

  ) { }

  ngOnInit() {
    this.credentials.username = window.localStorage.getItem('username') || '';
  }

  onLogin() {
    console.log("/api/auth/signin");
    this.securityService.login(this.credentials).subscribe((data) => {
      console.log(data);
      window.localStorage.setItem('username', this.credentials.username);
      this.router.navigate(['/home']);
    }, () => {
      this.messageService.add({ severity: 'error', summary: '', detail: 'Usuario o Contraseña Incorrectos' })
    });


    //console.log(data);
    window.localStorage.setItem('username', this.credentials.username);
    this.router.navigate(['/home']);
  }

}
