import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient, HttpHeaders }   from '@angular/common/http';
import { SecurityService } from './security.service';
import { environment }  from '../../environments/environment';

@Injectable({providedIn: 'root'})
export class DataService {

  navigationToggledEmiter: EventEmitter<any> =  new EventEmitter();
  navigationToggled: boolean = false;

  navigationOpenedEmiter: EventEmitter<any> =  new EventEmitter();
  navigationOpened: boolean = false;

  filters:any = {
    startDate: new Date(),
    endDate: new Date(),
    project:null
  }

  constructor(
    private http:HttpClient,
    private securityService: SecurityService
  ){}

  find(collection: String){
    return this.http.get<any>(environment.serverBaseURL + environment.api + collection+ "/",{
      headers: new HttpHeaders({
        "x-access-token": this.securityService.getToken()
      })
    });
  }

  findByParams(collection: String, params: String){
    return this.http.get<any>(environment.serverBaseURL + environment.api + collection + "?"+ params,{
      headers: new HttpHeaders({
        "x-access-token": this.securityService.getToken()
      })
    });
  }

  findBy(collection: String, query: String){
    return this.http.get<any>(environment.serverBaseURL + environment.api + collection + "/" + query, {
      headers: new HttpHeaders({
        "x-access-token": this.securityService.getToken()
      })
    });
  }

  findById(collection: String, id: String){
    return this.http.get<any>(environment.serverBaseURL + environment.api + collection + "/" + id, {
      headers: new HttpHeaders({
        "x-access-token": this.securityService.getToken()
      })
    });
  }

  insertOne(collection, obj){
    return this.http.post(environment.serverBaseURL + environment.api + collection + "/", obj, {
      headers: new HttpHeaders({
        "x-access-token": this.securityService.getToken()
      })
    });
  }

  insertMany(collection, list){
    return this.http.post(environment.serverBaseURL + environment.api + collection + "/", list, {
      headers: new HttpHeaders({
        "x-access-token": this.securityService.getToken()
      })
    });
  }

  updateOne(collection, obj){
    return this.http.put(environment.serverBaseURL + environment.api + collection + "/",  obj, {
      headers: new HttpHeaders({
        "x-access-token": this.securityService.getToken()
      })
    });
  }

  updateMany(collection, obj){
     return this.http.put(environment.serverBaseURL + environment.api + collection + "/",  obj, {
      headers: new HttpHeaders({
        "x-access-token": this.securityService.getToken()
      })
    });
  }

  deleteOne(collection, id){
    return this.http.delete(environment.serverBaseURL + environment.api + collection + "/" + id, {
      headers: new HttpHeaders({
        "x-access-token": this.securityService.getToken()
      })
    });
  }

  toggleLeftNavigation(){
    if (this.navigationToggled){
      this.navigationToggled = false;
    }else{
      this.navigationToggled = true;
    }
    this.navigationToggledEmiter.emit(this.navigationToggled);
  }

  toggleTopNavigation(){
    if (this.navigationOpened){
      this.navigationOpened = false;
    }else{
      this.navigationOpened = true;
    }
    this.navigationOpenedEmiter.emit(this.navigationOpened);
  }

  showLeftNavigation(){
    this.navigationToggled = false;
    this.navigationToggledEmiter.emit(this.navigationToggled);
  }

  formatLabel(str, maxwidth){
     var sections = [];
     var words = str.split(" ");
     var temp = "";

     words.forEach(function(item, index){
         if(temp.length > 0){
             var concat = temp + ' ' + item;
             if(concat.length > maxwidth){
                 sections.push(temp);
                 temp = "";
             }
             else{
                 if(index == (words.length-1))
                 {
                     sections.push(concat);
                     return;
                 }
                 else{
                     temp = concat;
                     return;
                 }
             }
         }

         if(index == (words.length-1)){
             sections.push(item);
             return;
         }

         if(item.length < maxwidth) {
             temp = item;
         }
         else {
             sections.push(item);
         }
     });

     return sections;
   }




}
