import { Component, OnInit, Input } from '@angular/core'
import { DataService } from '@services/data.service'

@Component({
  selector: 'app-project-stores',
  templateUrl: './project-stores.component.html',
})
export class ProjectStoresComponent implements OnInit {

  @Input("projectId") projectId: any

  stores: Array<any>
  store: any
  chains: Array<any>

  displayDeleteDialog: Boolean
  displayStoreDialog: Boolean

  filter: String

  constructor(
    private dataService:DataService
  ) { }

  ngOnInit() {
    this.onFindStores()
  }

  onFindStores(){
    this.dataService.findById("store", this.projectId).subscribe((data)=>{
      console.log("stores",data)
      this.stores = data
    })
  }

  onFindChains(){
    this.dataService.findById("chain", this.projectId).subscribe((data)=>{
      this.chains = data
    })
  }

  onShowDeleteDialog(store){
    this.store = store
    this.displayDeleteDialog = true
  }

  onEdit(store){
    console.log(store)
    this.store = store
    this.displayStoreDialog = true
    this.onFindChains()

  }

  onNew(){
    this.store = {}
    this.store.coordinates = {}
    this.displayStoreDialog = true
    this.onFindChains()
  }

  onCancel(){
    this.store = {}
    this.displayStoreDialog = false
  }

  onSave(){
    this.store.type="store"
    this.store.project=this.projectId
    this.dataService.insertOne("store", { store: this.store, id: this.projectId }).subscribe((data)=>{
      console.log(data)
      this.displayStoreDialog = false
      this.onFindStores()
    })
  }

  onDelete(){
    this.dataService.deleteOne("store", this.store._id + "/" + this.store._rev).subscribe((data)=>{
      console.log(data)
      this.displayDeleteDialog = false
      this.onFindStores()
    })
  }

}
