import { Component, OnInit } from '@angular/core';
import { DataService }        from '@services/data.service';
import * as _ from 'lodash';

@Component({
    selector: 'app-incidence',
    templateUrl: './incidence.component.html',
})
export class IncidenceComponent implements OnInit {

    projects:Array<any>;
    project:any;
    chains:Array<any>;
    chain:any;
    stores:Array<any>;
    store:any;
    incidences:any;

    startDate:any = new Date();
    endDate:any = new Date();

    constructor(
      private dataService: DataService,
    ) { }

    ngOnInit() {
      this.onFindProyects();
    }

    onChangeProject(){
      this.onFindChains();
      this.onFindStores();
    }

    onFindProyects(){
      this.dataService.find("project").subscribe((data) => {
        var projects = [];
        data.rows.forEach(function(element) {
          projects.push(element.doc);
        });
        this.projects = projects;
        this.project = this.projects[0] || {} ;
        this.onFindChains();
        this.onFindStores();
      });
    }

    onFindChains(){
      console.log("onFindChains");
      this.dataService.findById("chain", this.project._id).subscribe((data) => {
        this.chains = data;
        this.chain = this.chains[0] || "";
      });
    }

    onFindStores(){
      console.log("onFindStores");
      this.dataService.findBy("tickets/project", this.project._id + "/stores").subscribe((data) => {
        this.stores = data;
        this.store = this.stores[0] || "";
      });
    }

    onFind(){
      console.log("onFind");
      this.onFindIncidencesperProject();
    }

    onFindIncidencesperProject(){
      console.log("onFindTicketsperProject");
      this.dataService.findBy("incidences/project", this.project._id + '/chain/' + this.chain.name + "/store/" + this.store.key[2] +'/'+this.startDate+'/'+this.endDate).subscribe((data) => {
        this.incidences = data;
        console.log("incidences", data)
      });
    }

  }
