import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { SecurityService } from './services/security.service';

@Injectable({ providedIn: 'root' })
export class SecurityGuard implements CanActivate {

  constructor(
    private securityService: SecurityService,
    private router: Router
  ) { }

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    return true;
    if (this.securityService.isAuthenticated && !this.securityService.isTokenExpired()) {
      if (next.data.roles) {
        if (this.securityService.hasPermisions(next.data.roles)) {
          return true;
        } else {
          console.log("Access Denied:", state.url)
          this.router.navigateByUrl('/login')
          return false;
        }
      } else {
        return true;
      }
    } else {
      console.log("Access Denied: user not authenticated", state.url)
      this.router.navigateByUrl('/login');
      return false;
    }
  }
}
