import { Component, OnInit, ViewChild } from '@angular/core'
import { DataService } from '@services/data.service'
import * as _ from 'lodash'


@Component({
  selector: 'app-attendance',
  templateUrl: './attendance.component.html'
})
export class AttendanceComponent implements OnInit {

  @ViewChild('salesbyTeamChart', { static: false }) salesbyTeamChart
  @ViewChild('salesbyTeamLineChart', { static: false }) salesbyTeamLineChart

  colors = ['#4bc0c0', '#ec7063', '#b078c7', '#73c6b6', '#73c6b6', '#f6b7b0', '#f9e89b', '#808b96', '#885656']
  spiner = {}

  projects:any
  project:any
  startDate:any = new Date()
  endDate:any = new Date()
  teams: any
  team:any

  users:any



  constructor(
    private dataService: DataService,
  ) {

   }

  ngOnInit() {
    this.onFindProyects()
  }

  onFind(){
    this.onFindAssistance()
  }

  onChangeProject(){
    this.onFindTeamsByProyect()
  }

  onFindProyects(){
    this.dataService.find("project").subscribe((data) => {
      this.projects = data.rows
      this.project = this.projects[0] || ""
      this.onFindTeamsByProyect()
    })
  }

  onFindTeamsByProyect(){
    this.dataService.findBy('team', this.project.id).subscribe((data) => {
      console.log(data)
      this.teams = data
      this.team = this.teams[0]
    })
  }

  onFindAssistance(){
    this.spiner['users'] = true
    var startDate = this.startDate
    var endDate = this.endDate
    var team = ""
    if(this.team !== "*"){
      team = '/team/'+ this.team.name
    }
    this.dataService.findBy("shift/project", this.project.id + team +'/attendance/' + startDate+"/"+endDate).subscribe((data) => {
      console.log(data)
      this.users = data
      this.spiner['users'] = false
    })
  }

}
