import { Component, OnInit, ChangeDetectorRef } from '@angular/core'
import { DataService }        from '@services/data.service'
import { SecurityService } from '@services/security.service'
import { MessageService } from 'primeng/api';

import * as _ from 'lodash'

@Component({
  selector: 'app-inventory',
  templateUrl: './inventory.component.html',
  styleUrls: ['./inventory.component.css']
})
export class InventoryComponent implements OnInit {


  projects:Array<any>
  project:any
  chains:Array<any>
  chain:any
  stores:Array<any>
  store:any
  products:Array<any>
  documents:Array<any>
  spiner:boolean = false

  inventory:any
  items: Array<any>
  displayEdit:boolean = false
  displayProducts:boolean = false
  display:boolean = false
  photos: Array<any>

  startDate:any = new Date()
  endDate:any = new Date()

  constructor(
    private dataService: DataService,
    private securityService: SecurityService,
    private messageService:MessageService,
    private changeDetectorRef: ChangeDetectorRef
  ) { }

  ngOnInit() {
    var user = this.securityService.getCurrentUser()
    if(this.securityService.hasPermisions(['admin'])){
      this.onFindProyects()
    }else{
      this.onFindProyectsByUser(user._id)
    }
  }

  onFindProyects(){
    this.dataService.find("project").subscribe((data) => {
      var projects = []
      data.rows.forEach(function(element) {
        projects.push(element.doc)
      })
      this.projects = projects
      this.project = this.projects[0] || {}
      this.onFindChains()
    })
  }

  onFindProyectsByUser(userId){
    this.dataService.find("users/"+userId+"/projects").subscribe((data) => {
      this.projects = data.docs
      this.project = this.projects[0] || {}
      this.onFindChains()
    })
  }

  onChangeProject(){
    this.onFindChains()
  }

  onChangeChain(){
    this.onFindStores()
  }

  onFindChains(){
    console.log("onFindChains")
    this.dataService.findById("chain", this.project._id).subscribe((data) => {
      this.chains = data
      this.chain = this.chains[0] || ""
      this.onFindStores()
    })
  }

  onFindStores(){
    console.log("onFindStores", this.chain.name)
    this.dataService.findBy("stores/chain", this.chain.name).subscribe((data) => {
      this.stores = data
      this.store = this.stores[0] || ""
    })
  }

  onFind(){
    console.log("onFind")
    this.displayEdit = false
    this.onFindInventory()
    this.onFindProducts()
  }

  onFindProducts(){
    this.dataService.findById("product", this.project._id ).subscribe((data)=>{
      console.log("[onFindProducts]",data)
      this.products = data.rows;
       this.products = _.sortBy(this.products, [function(o) { return o.doc.name; }])
    })
  }

  onFindInventory(){
    console.log("onFindInventory")
    this.spiner = true
    this.dataService.findBy("inventory/project", this.project._id + '/chain/' + this.chain.name + "/store/" + this.store.doc.name +'/'+this.startDate+'/'+this.endDate).subscribe((data) => {
      this.documents = data
      this.spiner = false
    })
  }

  onSelect(inventory:any){
    this.inventory = inventory.doc
    this.items = _.sortBy(inventory.doc.inventory, [(o) => { return o.doc.name }])
    this.displayEdit = true
  }

  onAddProduct(product:any){
    console.log(product)
    console.log(this.items)
    if(this.items){
      let found = this.items.find(e =>{
        return e.doc.code == product.doc.code
      })
      if(!found){
        this.items.push(product)
        this.items = _.sortBy(this.items, [(o) => { return o.doc.name }])
        this.messageService.add({severity:'success', summary:'', detail:'Producto agregado'})
      }else{
        this.messageService.add({severity:'error', summary:'', detail:'El producto ya existe en el inventario'})
      }
      this.displayProducts = false
    }
  }



  onRemoveProduct(index:number){
    console.log("[onRemoveProduct]", index)
    if(this.items){
      this.items.splice(index,1)
    }
  }

  onCancel(){
    this.displayProducts = false
    this.onFind()
  }

  onSave(){
    this.inventory.inventory = this.items
    this.dataService.insertOne("inventory", {"inventory":this.inventory}).subscribe(()=>{
      this.messageService.add({severity:'success', summary:'', detail:'Inventario guardado'})
      this.displayEdit = false
      this.onFind()
    })
  }

}
