import { Component, OnInit } from '@angular/core'
import { DataService } from '@services/data.service'
import { ActivatedRoute, Params } from '@angular/router'
import { MessageService } from 'primeng/api';

import * as moment from 'moment'
@Component({
  selector: 'app-project',
  templateUrl: './project.component.html',
})
export class ProjectComponent implements OnInit {

  projectId:any
  project:any = {}
  supervisors: Array<any>
  startDate: Date
  endDate: Date

  constructor(
    private dataService:DataService,
    private route: ActivatedRoute,
    private messageService:MessageService
  ) { }

  ngOnInit() {
    this.route.params.subscribe((params : Params) => {
      this.projectId =  params["id"]
      if(this.projectId && this.projectId != "new"){
        this.onFindProject()
      }else{
        this.projectId = null
      }
    })
    this.onFindSupervisors()
  }

  onFindProject(){
    this.dataService.findById('project',this.projectId).subscribe((data)=>{
      this.project = data
      this.startDate = moment(this.project.start_date).toDate()
      this.endDate = moment(this.project.end_date).toDate()
    })
  }

  onFindSupervisors(){
    this.dataService.find('users/supervisors').subscribe((data)=>{
      this.supervisors = data
    })
  }

  onSave(){
    this.project.type="project"
    this.project.start_date = moment(this.startDate).toISOString()
    this.project.end_date = moment(this.endDate).toISOString()
    //save project
    this.dataService.insertOne('project', { project: this.project }).subscribe((data)=>{
      this.projectId = data["_id"]
      this.onFindProject()
      this.messageService.add({severity:'success', summary:'', detail:'Proyecto guardado '})
    })
    // update supervisor
    this.supervisors.forEach(function(supervisor) {
      console.log(supervisor.doc._id, this.project.supervisor)
      if(supervisor.doc._id === this.project.supervisor){
        supervisor.doc.project = this.project._id
        supervisor.doc.project_name = this.project.name
        console.log("saving", supervisor)

        this.dataService.insertOne("users", { user: supervisor.doc }).subscribe((data)=>{
          console.log("insertOne",data)
        })
      }
    }, this);
  }

}
